import React from 'react';
import { Line } from 'react-chartjs-2';

const Chart = ({ allweatherArr }) => {
    // console.log(allweatherArr);
    const data = {
        // labels: ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday'],
        labels: (allweatherArr[9]),
        datasets: [
            {
                label: 'Morning',
                // backgroundColor: `rgba(1, 126, 24, 0.53)`,
                borderColor: `#0000ff`,
                data: allweatherArr[0],

            },
            {
                label: 'Afternoon',
                // backgroundColor: `rgba(0, 255, 0 ,0.5)`,
                // backgroundColor: `rgba(126, 1, 89, 0.55)`,
                borderColor: `#00ff00`,
                data: allweatherArr[3],
            },
            {
                label: 'Evening',
                // backgroundColor: `rgba(139, 69, 19,0.5)`,
                // backgroundColor: `rgba(0,0,0,0.5)`,
                borderColor: `#ff0000`,
                data: allweatherArr[6],
            },
        ]
    }
    const options = {
        tooltips: {
            mode: 'index'
        },
        legend: {
            position: 'bottom',
            display: true,
            labels: {
                fontColor: 'black',
                fontSize: 12,
            }
        },

        title: {
            display: true,
            text: 'Temp Chart ',
            position: 'top',
            fontColor: 'white',
            fontSize: 30,
        },
        scales: {
            yAxes: [{
                scaleLabel: {
                    fontFamily: 'Yarta',
                    display: true,
                    labelString: 'Temp C',
                    fontColor: 'white',
                    fontSize: 15,
                },
                gridLines: {
                    color: 'white'
                },
                ticks: {
                    fontFamily: 'Yarta',
                    fontSize: 15,
                    fontColor: 'white',

                }
            }],
            xAxes: [{
                scaleLabel: {
                    fontFamily: 'Yarta',
                    display: true,
                    fontColor: 'white',
                    fontSize: 15,
                },
                gridLines: {
                    color: 'white'
                }

            }]
        }

    }
    return (
        <div>
            <div>
                <Line data={data} options={options} />
            </div>
        </div>
    );
};

export default Chart;